<template>
    <div class="clasificacion">
        <div class="container-fluid">
            <center>
                <h1>GENERAL</h1>
            </center>
            <hr>
            <div class="container">
                <div class="row">
                    <div class="col-sm">
                        <div class="card">

                            <!--v-for="fila in apuestas" :key="fila.idApuesta" :value="fila.idApuesta"-->
                            <div class="card-body">
                                <h2 class="card-title">APUESTAS REALIZADAS</h2>
                                {{apuestasRealizadas.length}}
                            </div>
                        </div>
                    </div>

                    <div class="col-sm">
                        <div class="card">
                            <div class="card-body" id="ganadas">
                                <h2 class="card-title">APUESTAS GANADAS</h2>
                                {{apuestasg.length}}
                            </div>
                        </div>
                    </div>
                    <div class="col-sm">
                        <div class="card">
                            <div class="card-body" id="perdidas">
                                <h2 class="card-title">APUESTAS PERDIDAS</h2>
                                {{apuestasp.length}}
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>


<script>
    import axios from "axios"
    import $ from "jquery"
    export default {
        data() {
            return {
                apuestas: [],
                apuestasg: [],
                apuestasRealizadas: [],
                apuestasp: []



            }



        },

        created() {
            this.listarApuestas();
            this.apuestasGanadas();

            this.apuestasPerdidas();



        },
        methods: {
/*/
            Apuestas() {

                let list = this;
                axios.get('api/Apuestas/Historial').then(function (response) {
                    list.apuestas = response.data;

                })

            },
    */

            listarApuestas() {
                let list = this;
                axios.get('api/Apuestas/Filtrar/' + this.$store.getters.getToken.Id).then(function (response) {
                    list.apuestasRealizadas = response.data;
                })


            },

            apuestasGanadas() {
                let list = this;
                axios.get('api/Apuestas/ApuestasGanados/' + this.$store.getters.getToken.Id).then(function (response) {
                    list.apuestasg = response.data;
                })
            },


            apuestasPerdidas() {
                let list = this;
                axios.get('api/Apuestas/ApuestasPerdidos/' + this.$store.getters.getToken.Id).then(function (response) {
                    list.apuestasp = response.data;
                })
            },


            /*
                 FiltrarApuestas(){

              
                this.apuestas.filter(dato => {
                    if (dato.idUsario == this.$store.getters.getToken.Id) {
                        this.partido=dato.partidoApostado
                    }
                });
                
            }*/
        }
    }
</script>



<style scoped>
    h1 {
        font-size: 2rem
    }

    h2 {
        font-size: 1.2rem
    }

    #ganadas{
        border: 1px solid greenyellow;
    }

       #perdidas{
        border: 1px solid rgb(161, 9, 9);
    }
</style>