<script>
	import axios from "axios"
	import $ from "jquery"

	export default {
		/*
		name: 'App',
		components: {
			Hooper,
			Slide
		},*/
		data() {
			return {
				registro: true,
				registrar: false,
				nombre: '',
				ap1: '',
				usuario: '',
				pass: '',
				rpass: '',
				email: '',
				fechNac: '',
				enviando: false, //boton envia
				errores: [],
				correcto: false



			}
		},
		methods: {




			mostrarRegistrar() {
				if (this.registrar == false) {
					this.registrar = true;
				} else {
					this.registrar = false;
				}


			},


			ValidarRegistro() {


				let exp1 = /^[a-zA-Z0-9._-]{4,12}([0-9]{1,5})?$/;
				let resp1 = exp1.test(this.usuario);

				let exp2 = /^[A-Z]{1}[a-z]{2,10}[._-]{1}[0-9]{2,10}$/;
				let resp2 = exp2.test(this.pass);

				let exp3 = /^([a-zA-z]{3,12})$/;
				let resp3 = exp3.test(this.nombre);

				let exp4 = /^([a-zA-Z]+[ ]?){1,2}$/;
				let resp4 = exp4.test(this.ap1);

				let exp5 = /^[a-zA-Z0-9._-]+@[a-zA-Z]{4,15}.[a-z]{2,10}$/;
				let resp5 = exp5.test(this.email);



				if (resp3 == null || resp3.length == 0 || resp3 == false) {
					this.errores.push('Nombre incorrecto o formato inválido');

					return false;
				} else if (resp4 == null || resp4.length == 0 || resp4 == false) {
					this.errores.push('Apellido/s incorrecto/s o formato inválido');
					return false;
				} else if (resp1 == null || resp1.length == 0 || resp1 == false) {
					this.errores.push("Nombre de usuario incorrecto o ya existe");
					return false;
				} else if (resp5 == null || resp5.length == 0 || resp5 == false) {
					this.errores.push('Formato de correo electrónico incorrecto');
					return false;
				} else if (resp2 == null || resp2.length == 0 || resp2 == false) {
					this.errores.push('Formato de contraseña incorrecta');
					return false;
				} else if (this.pass != this.rpass) {
					this.errores.push('La contraseña no coincide');
					return false;
				} else if (this.fechNac == null || this.fechNac == 0 || this.fechNac == false) {
					this.errores.push('Campo de fecha de Nacimiento obligatorio');
					return false;
				} else {
					this.errores = []

					event.toElement.disabled = true;
					this.enviando = true;
					setTimeout(() => this.correcto = true, 6000);
					setTimeout(() => this.enviando = false, 6000);

				}






				if (this.registro == true) {
					axios.post('api/Usuarios/Registrar', {
						'usuario': this.usuario,
						'pass': this.pass,
						'nombre': this.nombre,
						'ap1': this.ap1,
						'email': this.email,
						'fechaNacimiento': this.fechNac,
					}).then(function (response) {

					})
				} else {
					alert("Error");
				}

			}
		}
	}
</script>


