<template>
    <div class="equipos">

        <div id="page-content-wrapper">
            <div class="container-fluid">
                <div class="row" id="row1">
                    <div class="col" v-for="fila in equipos" :key="fila.idEquipo" :value="fila.idEquipo">
                        <img :src="'data:image.jpeg;base64' + fila.imgEquipo" alt="Logo" width="20px" height="25px">
                    </div>
                    <span>Seleccione un equipo para obtener información relevante:</span>
                    <select class="custom-select mr-sm-2" id="inlineFormCustomSelect" v-model="valores" @change="obtenerValores()">
                        <option v-for="fila in equipos" :key="fila.idEquipo" :value="fila.idEquipo">
                            {{fila.nombreEquipo}}
                        </option>
                    </select>

                </div>

            </div>
        </div>

        <!--
        <div class="container">
            <div class="row" id="row1">
                <div class="col" v-for="fila in equipos" :key="fila.idEquipo" :value="fila.idEquipo">
                    <img :src="'data:image.jpeg;base64' + fila.imgEquipo" alt="Logo" width="20px" height="25px">
                </div>
                <span>Seleccione un equipo para obtener información relevante:</span>
                <select v-model="valores" @change="obtenerValores()">
                    <option v-for="fila in equipos" :key="fila.idEquipo" :value="fila.idEquipo">
                        {{fila.nombreEquipo}}
                    </option>
                </select>

            </div>

-->
        <div class="container">
            <ul class="list-group list-group-flush">
                <li class="list-group-item"><span class="badge">Temporada:</span>{{temporada}}</li>
                <li class="list-group-item"><span class="badge">Club: </span>{{nombreEquipo}}</li>
                <li class="list-group-item"><span class="badge">Entrenador: </span>{{entrenador}}</li>
                <li class="list-group-item"><span class="badge">País: </span>{{paisOrigen}}</li>
            </ul>
        </div>




        <table class="table table-bordered table-dark">
            <thead>
                <tr>
                    <th scope="col">JUGADOR</th>
                    <th scope="col">POSICION</th>
                    <th scope="col">DORSAL</th>
                    <th scope="col">EDAD</th>

                </tr>
            </thead>
            <tbody>
                <tr v-for="fila in jugadoresSeleccionados" :key="fila.idJugador" :value="fila.idJugador">

                    <td>{{fila.jugador}}</td>
                    <td>{{fila.posicion}}</td>
                    <td>{{fila.dorsal}}</td>
                    <td>{{fila.edad}} años</td>
                </tr>
            </tbody>
        </table>

    


    </div>
</template>

<script>
    import axios from "axios"

    export default {


        data() {
            return {
                //EQUIPOS
                equipos: [],
                idLiga: 1,
                valores: '1',
                nombreEquipo: '',
                imgEquipo: '',
                entrenador: '',
                paisOrigen: '',
                temporada: '',
                //JUGADORES
                jugadores: [],
                jugadoresSeleccionados: [],
                nombreJugador: '',
                idEquipo: '1'


            }
        },
        created() {
            this.listarEquipos();
            this.listarJugadores();

        },

        methods: {

            listarEquipos() {
                let list = this;
                axios.get('api/Equipos/Listar/' + list.idLiga).then(function (response) {
                    list.equipos = response.data;
                    list.obtenerValores();
                })

            },

            listarJugadores() {
                let list = this;
                axios.get('api/Jugadores/Listar').then(function (response) {
                    list.jugadores = response.data;
                    list.obtenerValores();

                })
            },

            obtenerValores() {
                let me = this;
                me.jugadoresSeleccionados = [];
                this.equipos.filter(dato => {
                    if (dato.idEquipo == me.valores) {
                        me.nombreEquipo = dato.nombreEquipo;
                        me.imgEquipo = dato.imgEquipo;
                        me.entrenador = dato.entrenador;
                        me.paisOrigen = dato.paisOrigen;
                        me.temporada = dato.idTemporada;

                    }
                });

                this.jugadores.filter(datoJ => {
                    // console.log(datoJ.idEquipo)
                    // console.log(me.valores)
                    if (datoJ.idEquipo == me.valores) {
                        me.jugadoresSeleccionados.push(datoJ);
                    }
                })

            },

        }
    }
</script>


<style scoped>
    .container {
        text-align: center;
        border-radius: 10px 10px 10px 10px;
        -moz-border-radius: 10px 10px 10px 10px;
        -webkit-border-radius: 10px 10px 10px 10px;
        border: 1px inherit #000000;
    }

    #row1 {
        justify-content: center;
    }



    /*SELECT*/



    select {
        width: 100%;
        text-align: center;
        background-color: white;
        display: inline-block;
        font: inherit;
        line-height: 1.5em;
        padding: 0.5em 3.5em 0.5em 1em;
        margin: 0;
    
    }

    option {
        background: rgba(30, 83, 107, 0.021);
        width: 50%;

    }

    select,
    option {
        text-align: center;
        justify-content: center;
    }


</style>