<template>
  <div class="clasificacion">
    <table class="table">
  <thead class="thead-dark">
    <tr>
      <th scope="col">POSICION</th>
      <th scope="col">CLUB</th>
      <th scope="col">PUNTOS</th>
      <th scope="col">GANADOS</th>
            <th scope="col">PERDIDOS</th>
            <th scope="col">EMPATADOS</th>
            <th scope="col">GOLES A FAVOR</th>
            <th scope="col">GOLES EN CONTRA</th>
            <th scope="col">GOLES DIFERENCIA</th>

    </tr>
  </thead>
  <tbody>
    <tr v-for="(fila,index) in ordenar" :key="fila.idEquipo" :value="fila.idEquipo">
      <td> {{++index}}</td> 
      <td>{{fila.nombreEquipo}}</td>
      <td>{{fila.puntos}}</td>
            <td>{{fila.partidosGanados}}</td>
            <td>{{fila.partidosEmpatados}}</td>
            <td>{{fila.partidosPerdidos}}</td>
            <td>{{fila.golesFavor}}</td>
                        <td>{{fila.golesContra}}</td>
                                    <td>{{fila.golesDiferencia}}</td>
                            
</tr>
  </tbody>
</table>

  


  </div>
</template>


<script>
 import axios from "axios"
export default {
  data (){
    return {
      equipos: []
      
    }
  },
  created(){
    this.listarEquipos()
  },

computed: {

ordenar(){
      const asc = (a,b) => b.puntos - a.puntos || a.golesFavor - b.golesFavor
    
      return this.equipos.sort(asc)
}
},
  

  methods:{
    listarEquipos() {
                let list = this;
                axios.get('api/Equipos/ListarTodo').then(function (response) {
                    list.equipos = response.data;

                })
            },

           
  }
  
}
</script>



