import Vue from 'vue'
import Router from 'vue-router'

import Home from './views/Home.vue'
import Extras from './views/Extras.vue'
import Info from './views/Info.vue'
import Historial from './views/Historial.vue'


import Apuesta from './views/Apuestas.vue'

import stats from './views/Stats.vue'
import Equipos from './views/Equipos.vue'
import Clasificacion from './views/Clasificacion.vue'


Vue.use(Router)


export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [

    {
      path: '/',
      name: 'home',
      component: Home,
      /*,
      children:[{
        path: '/registrar',
        component: registrar,

      }]*/
    },
    {
      path: '/stats',
      name: 'stats',
      component: stats,
      redirect: '/Equipos',
      children: [{
        path: '/Equipos',
        component: Equipos,
      }, {
        path: '/Clasificacion',
        component: Clasificacion,
      }, ]

    },


    {
      path: '/Apuestas',
      name: 'apuestas',
      component: Apuesta,
      /*  redirect:'/Apuestas',
      children: [{
         path: '/Apuestas',
         component:Apuesta, 
       },{
         path: '/Apuestas/futbol',
         component:futbol, 
       },{
         path: '/Apuestas/baloncesto',
         component:baloncesto,
       },{
         path: '/Apuestas/motor',
         component:motor,
       },{
         path: '/Apuestas/tenis',
         component:tenis,
       }],*/
    },{
      path:'/Extras',
      name:'extras',
      component:Extras,
      redirect: '/Info',
      children: [{
        path: '/Info',
        component: Info,
      },{
        path: '/Historial',
        component: Historial,
      },  ]
      
    }
  ]
})