  <script>
    import $ from "jquery"
    import axios from "axios"


    export default {
      data() {
        return {
          usuario: '',
          show: true,
          pass: '',
          cargar: false,
          quitar1: false, //Mostrar Boton iniciado sesion
          mostrar1: true, //Ocultar el boton modal
          menu: false,
          errores: [],
          opciones:false

        }
      }, 
      created() {


        let me = this;
        me.$store.dispatch('autoLogin');

        me.$store.dispatch('cerrarSesion',null);

        /*if (this.$store.state.token != null) {
          me.quitar1 = true;
          me.mostrar1 = false;
        }*/


      },

      mounted() {
        window.addEventListener('resize', this.onResize)

      },
     
      methods: {
        //Devuelve un valor true con el objetivo de que aparezca el menu
        onResize(event) {
          // console.log('window has been resized', event)
          if (window.innerWidth >= 1024) {
            this.show = true;
          }
        },

        cambiar() {
          if (this.menu == true) {
            this.menu = false;
          } else {
            this.menu = true;
          }
        },

        validarLogin() {
          let me = this;
          let exp1 = /^[a-zA-Z0-9._-]{4,12}([0-9]{1,5})?$/;
          let resp1 = exp1.test(this.usuario);


          let exp2 = /^[A-Z]{1}[a-z]{2,10}[._-]{1}[0-9]{2,10}$/;
          let resp2 = exp2.test(this.pass);
          //alert(resp2);

          if (resp1 == null || resp1.length == 0 || resp1 == false) {
            this.errores.push("Usuario incorrecto");
            return false;
          }else{
            
            this.errores=[]

          }
           
           if (resp2 == null || resp2.length == 0 || resp2 == false) {
            this.errores.push("Contraseña incorrecta");
            return false;
          }else{
               this.errores=[]
          }

          if (this.cargar == false) {
            axios.post('api/Usuarios/Login', {
              'usuario': this.usuario,
              'pass': this.pass
            }).then(function (response) {

              if (response.data == false) {
                me.errores.push("Datos incorrectos")
              } else {
               // console.log(response.data);
                me.$store.dispatch("iniciarSesion", response.data.token);
                $('#myModal').modal('toggle');
                 if(me.$store.getters.getToken.Id !=null){
    me.opciones=true
  }
                
                me.quitar1 = true;
                me.mostrar1 = false;


              }
            })
          }
        },
        cerrarSesion() {
          let me = this;
          me.$store.dispatch('cerrarSesion');
          location.reload();
        },

        aparecerRegistro() {
          $('#myModal').modal('toggle');

        },


ocultarOpciones(){
  if(this.$store.getters.getToken.Id !=null){
    this.opciones=true
  }
}




      }
    }
  </script>

