import Vue from 'vue'
import Vuex from 'vuex'
import route from './router'
import decode from 'jwt-decode'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
   token:null
  },
  getters:{
    getToken(state){
      if(state.token){
        return decode(state.token)
      }else{
        return null
      }
    }
  },
  mutations: {
   setToken(state,tokenRecibido){
     state.token = tokenRecibido;
   }
  },
  actions: {
    iniciarSesion({commit},tokenRecibido){
      commit("setToken",tokenRecibido)
      localStorage.setItem("token",tokenRecibido)
     // me.$route.push({path:'/Apuestas'})
     route.push({name:'apuestas'});

      
    },
    autoLogin({commit}){
      let token = localStorage.getItem("token");
     

      if(token){
        commit("setToken",token);
        route.push({name:'apuestas'});

      }else{
        route.push({name:'home'})
      }
    },
    cerrarSesion({commit}){
      commit("setToken",null)
      localStorage.removeItem("token");
      route.push({name:'home'})
    }
  }
})
