<script>
  import axios from "axios"
  import $ from "jquery"


  export default {

    data() {
      return {
        partidos: [],
        acumulacionPartidos: [], //Partidos apostados
        menu: false,
        importe: false,
        importeUnidad: true,
        apuestasCorrectas:false,
        //BUSCAR EN LA TABLA  Y PAGINACION
        ocultarPaginacionText: true,
        buscar: '',
        xpagina: 4,
        contador: 0,
        paginaActual: 1,
        paginas: 0,



        //CrearApuestas
        fecha: '',
        hora: '',


        //CALCULOS DE APUESTAS
        sumaGanancias: [],
        totalApostado: 0, //total del importe apostado en los partido/s
        resultadoIndividual: [], //acumulacion de multiplicar cuota por euro apostado de cada partido
        resultadoApostado: 0, //suma los resultados individuales para obtener el resultado total de los partidos apostados

        //FILTRAR PARTIDOs
        filtro: '',
        tablaActiva: true,
        ocultarRadios: true,

        //mostrar saldo de su respectivo usuario
        saldo: [],
        total: 0,

        //actualizar PUT
        condicion: true,





      }
    },
    created() {
      this.listarPartidos();

    },
    computed: {

      partidosProximos() {

        let dateTime = new Date();
        let dateStr = JSON.stringify(dateTime); //pasas de Date nativo a String 


        this.fecha = JSON.parse(dateStr); //cambias de String a DateTime JSON
        console.log(this.fecha);


        let me = this;
        return this.partidos.filter(dato => {
          if (this.filtro == dato.estado) {
            return dato;
          }
        })
      },

      buscarFiltro() {
        let me = this;
        return this.filtro.slice(this.contador, this.contador + this.pagina)
      },


      /*
            allApostado(){

              
              this.totalApostado = 0;
              for (let index = 0; index < this.sumaGanancias.length; index++) {
                this.totalApostado = this.totalApostado + parseInt(this.sumaGanancias[index]);
                return (this.totalApostado);
              }

            },
            */


      buscarDato() {
        let me = this
        return this.partidos.filter(dato => {
          if (dato.nombreLocal.toLowerCase().indexOf(me.buscar.toLowerCase()) != -1 || dato.nombreVisitante
            .toLowerCase().indexOf(me.buscar.toLowerCase()) != -1 || dato.fechaPartido.indexOf(me.buscar) != -1) {
            return dato
          }
        })
      },


      crearPaginacion() {

        let me = this;
        return this.partidos.slice(this.contador, this.contador + this.xpagina)

      },



      calcularPag() {

        let calculo = this.partidos.length / this.xpagina;
        // 9/2=4,5
        //console.log(calculo)
        this.contador = 0
        this.paginaActual = 1;
        return this.paginas = Math.ceil(calculo);
      },

      //quitar botones de partidos finalizados,

      /*  botonesPartidoFinalizado() {
          this.partidos.filter(c => {
            if (c.estado == this.terminado) {
              $('input[type="button"]').prop("disabled", true);


            }
          })

        }*/

      /*
              valoresGanancias(index, item, event) {
         
              this.sumaGanancias[index] = parseFloat(event.target.value);
              console.log(event.target + "esto q es?")
              console.log(this.sumaGanancias);
              this.totalApostado = 0;
              for (let index = 0; index < this.sumaGanancias.length; index++) {
                this.totalApostado = this.totalApostado + this.sumaGanancias[index];
                console.log(this.totalApostado); //suma del importe total apostado
              }
              //se calcula el resultado de cada partido multiplicando el importe apostado por el valor de la cuota,
              //cuyo resultado se redondea a dos digitos
              this.resultadoIndividual[index] = parseFloat(this.sumaGanancias[index] * item.boton.value).toFixed(2);
              parseFloat(this.resultadoIndividual[index]) //canbiar de string a double
            },
      */

 restar(item) {
        let me = this;
        me.resta =me.saldo
          me.acumulacionPartidos.filter(function (valor) {
          me.resta -=  parseFloat(valor.valorNumber);
          console.log(me.resta)

          if(me.resta<=0){
            me.resta=0
          }



        })
        return parseFloat(me.resta)


      },



      importeTotal(item) {
        let me = this;
        let total = 0;
        this.acumulacionPartidos.filter(function (valor) {
          total = total + parseFloat(valor.valorNumber);
          parseFloat(total)
          console.log(total)
          me.total = total;

          if (me.total >= me.saldo) {
            $("input[type='number']").prop('max', valor.valorNumber);

            $('#apostar').prop("disabled", true);

          }

          if (me.total <= me.saldo) {
            $('#apostar').prop("disabled", false);

          }

          if(valor.valorNumber== NaN){
             return valor.valorNumber=0
          }



        })
        return parseFloat(total)

      },


      
     



    },
    methods: {
      aparecerMenu() {
        if (this.menu == true) {
          this.menu = false;
        } else {
          this.menu = true;
        }
      },
      aparecerImporte() {
        if (this.importe == false) {
          this.importe = true;
        }

      },
      cancelarImporteTodo(event) { //volver a activar los botones DE MANERA COLECTIVA y borrar las apuestas
        if (this.importe == true) {
          this.importe = false
          $('input[type="button"]').prop("disabled", false);

         // console.log(event.toElement.disabled)
          //console.log(event.toElement)
          this.acumulacionPartidos = [];
          this.sumaGanancias = [];
          this.totalApostado = 0;
        }

        /*  if(this.acumulacionPartidos !=0){
            this.importe =false;
            this.sumaGanancias=[];
            this.totalApostado=0;
          }*/
      },

      cancelarImporteUnidad(index, event) {
        //Eliminar un partido individual y si no hay ninguno que me oculte el IMPORTE
        //ACTIVAR BOTONES DE MANERA INDIVIDUAL
        if (this.importeUnidad == true) {
          this.importeUnidad == false
          this.acumulacionPartidos[index].boton.disabled = false;
          this.acumulacionPartidos.splice(index, 1);


        }

        if (this.acumulacionPartidos == 0) {
          this.importe = false;
        }
        // this.sumaGanancias.splice(index, 1);
        // this.resultadoIndividual.splice(index, 1);

        //  var indice = this.sumaGanancias.indexOf(index); // obtenemos el indice
        // this.sumaGanancias.splice(index, 1);

        /*
                  for (let index = 0; index < this.sumaGanancias.length; index++) {
                     console.log(this.totalApostado +"total apostado")
                    console.log(this.sumaGanancias[index] +"suma ganancias");
                    this.totalApostado = this.totalApostado - this.sumaGanancias[index];
                   
                    
                  }
                  
                  console.log(this.totalApostado +"total apostado")
                  if(this.totalApostado <=0){
                      this.totalApostado =0;
                    }

                  //para el ultimo
                  if (this.acumulacionPartidos == 0) {
                    this.importe = false;
                    this.sumaGanancias = [];
                    this.totalApostado = 0;
                  }
                }
        */
      },

      listarPartidos() { //Listar partidos, tanto acabados como por empezar
        let list = this;
        axios.get('api/Partidos/Listar').then(function (response) {
          list.partidos = response.data;

        })

      },
      activarImporte(item, event) { //desactivar los botones y agregar el partido en IMPORTE

        let me = this;

        event.toElement.disabled = true;


        switch (event.toElement.className) {
          case "local":

            this.acumulacionPartidos.push({
              idPartido: item.idPartido,
              nombre: item.nombreLocal, //id de partido y nombre de equipo apostado
              fecha: item.fechaPartido,
              hora: item.horarioPartidoInicio,
              boton: event.toElement,
              partido: item.nombreLocal + " vs " + item.nombreVisitante,
              cuotaLocal: item.cuotaLocal,
              golesLocal: item.golesLocal,
              valorNumber: event.target.value,
            });
            break;
          case "empate":
            this.acumulacionPartidos.push({
              idPartido: item.idPartido,
              nombre: "empate",
              fecha: item.fechaPartido,
              hora: item.horarioPartidoInicio,
              boton: event.toElement,
              partido: item.nombreLocal + " vs " + item.nombreVisitante,
              cuotaEmpate: item.cuotaEmpate,
              valorNumber: event.target.value,

            });
            break;
          case "visitante":
            this.acumulacionPartidos.push({
              idPartido: item.idPartido,
              nombre: item.nombreVisitante, //id de partido y nombre de equipo apostado
              fecha: item.fechaPartido,
              hora: item.horarioPartidoInicio,
              boton: event.toElement,
              partido: item.nombreLocal + " vs " + item.nombreVisitante,
              cuotaVisitante: item.cuotaVisitante,
              golesVisitante: item.golesVisitante,
              valorNumber: event.target.value,

            });
            break;
        }



      },
      //PAGINACION

      siguiente() {
        if (this.paginaActual > 0 && this.paginaActual != this.paginas) {
          this.paginaActual++;
          this.contador = this.contador + this.xpagina;
        }
      },
      atras() {
        if (this.paginaActual <= this.paginas && this.paginaActual != 1) {
          this.paginaActual--;
          this.contador = this.contador - this.xpagina;
        }
