      /*CREAR APUESTA EN BD*/

      crearApuestas() {

        //console.log(this.$store.getters.getToken.Id)
        let dateTime = new Date();
        let dateStr = JSON.stringify(dateTime); //pasas de Date nativo a String 


        this.fecha = JSON.parse(dateStr); //cambias de String a DateTime JSON
        //console.log(this.fecha);

        let me=this;


        //this.hora=hoy.getHours()+':'+hoy.getMinutes()+':'+hoy.getSeconds();
        // this.fecha = new Date().getFullYear()+'-'+("0"+(new Date().getMonth()+1)).slice(-2)+'-'+("0"+new Date().getDate()).slice(-2)

        // let fechaExacta= this.fecha + ' '+this.hora;


        for (let index = 0; index < this.acumulacionPartidos.length; index++) {
          console.log(this.acumulacionPartidos[index].boton);
          axios.post('api/Apuestas/Crear', {
            'FechaApuesta': this.fecha,
            'idPartido': this.acumulacionPartidos[index].idPartido,
            'idUsuario': this.$store.getters.getToken.Id,
            'GananciasPrevistas': this.resultadoIndividual[index],
            'NombreEquipoApostado': this.acumulacionPartidos[index].nombre, //Nombre de Equipo apostado
            'PartidoApostado': this.acumulacionPartidos[index].partido,
            'dineroApostado': this.acumulacionPartidos[index].valorNumber,
            'cuotaSeleccionada': this.acumulacionPartidos[index].boton.value,

          }).then(function (response) {
            me.apuestasCorrectas=true;
					setTimeout(() => me.apuestasCorrectas = false, 6000);


          })
        }


      },



      ocultarTablas() { // En caso de que buscar este lllenando, desactivamos los radio y lo ocultamos
        //sino ocultamos la primera tabla y aparecemos los radio

        if (this.buscar != "") {
          this.tablaActiva = true
          $('input[type="radio"]').prop("checked", false);
          this.ocultarRadios = false;
        } else {
          this.tablaActiva = false
          this.buscar = "";
          this.ocultarRadios = true;

        }
      },

      restarDinero() {



        let me = this;

        if (this.$store.state.token != null) {
          axios.get('api/Usuarios/MostrarSaldo/' + this.$store.getters.getToken.Id).then(function (response) {

            me.saldo = response.data;
          })

          this.$router.push({
            name: 'apuestas'
          });

        }



