      multiplicacionCuota(index, item) {
        let sumaTotal = 0
    


        if (item.cuotaLocal != null) {
          sumaTotal = parseFloat(item.valorNumber * item.cuotaLocal).toFixed(2)


        } else if (item.cuotaEmpate != null) {
          sumaTotal = parseFloat(item.valorNumber * item.cuotaEmpate).toFixed(2)


        } else if (item.cuotaVisitante != null) {
          sumaTotal = parseFloat(item.valorNumber * item.cuotaVisitante).toFixed(2)


        }


        this.resultadoIndividual[index] = sumaTotal; //canbiar de string a double
       // console.log(this.resultadoIndividual + " Es el resultado individual"); // multiplicacion individual de cada apuesta

        return parseFloat(sumaTotal).toFixed(2)

        /*  let multiplicar = parseFloat(this.sumaGanancias[index] * item.valorNumber).toFixed(2);
         return multiplicar */

      },


      actualizarSaldo(index) {

        for (let index = 0; index < this.acumulacionPartidos.length; index++) {

          axios.put('api/Usuarios/ActualizarSaldo', {
            'idUsuario': this.$store.getters.getToken.Id,
            'saldo': this.resta,
          }).then(function (response) {

          })

        }
      }





